import React, { useState, useEffect } from 'react';
import ReactMarkdown from 'react-markdown';
// import Image from 'next/image'; // Assuming you're using next/image. Adjust if you're using a different Image component.
import halloContent from './text/Hallo.md';
// import geraldinoUku from './images/geraldino_uku.jpg';

const Hallo = () => {
    const [markdown, setMarkdown] = useState('');

    useEffect(() => {
        fetch(halloContent)
            .then((response) => response.text())
            .then((text) => setMarkdown(text))
            .catch((error) => console.error('Error fetching markdown:', error));
    }, []);

    const components = {
        p: ({ node, children }) => {
            if (node.children[0] && node.children[0].tagName === "img") {
                const image = node.children[0];
                const metastring = image.properties.alt;
                const alt = metastring?.replace(/ *\{[^)]*\} */g, "");
                const metaWidth = metastring.match(/{([^}]+)x/);
                const metaHeight = metastring.match(/x([^}]+)}/);
                const width = metaWidth ? metaWidth[1] : "768";
                const height = metaHeight ? metaHeight[1] : "432";
                const isPriority = metastring?.toLowerCase().match('{priority}');
                const hasCaption = metastring?.toLowerCase().includes('{caption:');
                const caption = metastring?.match(/{caption: (.*?)}/)?.pop();
    
                return (
                    <div className="postImgWrapper">
                        <img
                            src={image.properties.src}
                            width={width}
                            height={height}
                            className="postImg"
                            alt={alt}
                        />
                        {hasCaption ? <div className="caption" aria-label={caption}>{caption}</div> : null}
                    </div>
                );
            }
            return <p>{children}</p>;
        }
    };    

    return (
        <div>
            {/* <img src={geraldinoUku} alt="GeraldinoTisch"/> */}
            <ReactMarkdown components={components}>{markdown}</ReactMarkdown>
        </div>
    );
};

export default Hallo;
